import { Component, Input, OnChanges } from '@angular/core';
import {
    DashboardServerIdsResponseItem, ServerSeverity,
} from 'app/components/dashboard/dashboard.service';

@Component({
    selector: 'i360-recommendations',
    templateUrl: './recommendations.component.html',
    styleUrls: ['./recommendations.component.scss'],
})
export class RecommendationsComponent implements OnChanges {
    ServerSeverity = ServerSeverity;
    @Input() item: DashboardServerIdsResponseItem;
    noRecommendations: boolean;
    issuesCount: number;
    ngOnChanges() {
        if (this.item) {
            this.noRecommendations = !this.item.recommendations.good.length
                && !this.item.recommendations.medium.length
                && !this.item.recommendations.poor.length;
            this.issuesCount = this.item.recommendations.poor.length +
                this.item.recommendations.medium.length;
        }
    }
}
