import {
    Directive,
    ElementRef,
    Input,
    OnChanges,
    OnDestroy,
    SimpleChanges,
} from '@angular/core';
import { Chart, ChartType, registerables } from 'chart.js';
import { TranslateService } from 'app/utils/translate/translate.service';
import { fromEvent, debounceTime } from 'rxjs';

@Directive({
    selector: 'canvas[baseDoughnut]',
})
export class DoughnutDirective implements OnDestroy, OnChanges {
    @Input() data: number[] = [];
    @Input() percents: string[] = [];
    chart: any;
    spam: string = '';
    ham: string = '';

    private langChangeSubscription = this.translate.translate.onLangChange.subscribe(() => {
        this.update();
    });

    // native ChartJS resizing doesn't work well in DirectAdmin Evolution skin
    private resizeSubscription = fromEvent(window, 'resize').pipe(
        debounceTime(400),
    ).subscribe(() => {
        this.chart.resize();
    });

    constructor(
        public element: ElementRef,
        public translate: TranslateService,
    ) {
        this.updateTranslation();
        Chart.register(...registerables);

        let context = this.element.nativeElement.getContext('2d');
        let options = {
            type: 'doughnut' as ChartType,
            data: this.getData(),
            options: {
                aspectRatio: 1.6666666,
                maintainAspectRatio: false,
                elements: {
                    arc: {
                      borderWidth: 0.5,
                    }
                },
                cutout: '65%',
                plugins: {
                    legend: {
                        labels: {
                            usePointStyle: true,
                            fontColor: '#000',
                        },
                    },
                    tooltips: {
                        callbacks: {
                            label: (tooltipItems, data) => {
                                return this.getLabels()[tooltipItems.index];
                            }
                        }
                    },
                    tooltip: {
                        usePointStyle: true,
                        callbacks: {
                            labelPointStyle: function(context) {
                                return {
                                    pointStyle: 'circle' as 'circle',
                                    rotation: 0,
                                };
                            }
                        },
                        boxPadding: 3,
                        caretSize: 0,
                        cornerRadius: 3,
                    },
                },
            },
            plugins: [{
                id: 'legendMargin',
                beforeInit: (chart, legend, options) => {
                    const originalFit = chart.legend.fit;
                    chart.legend.fit = function fit() {
                        // to use `this` correctly inside it
                        originalFit.bind(chart.legend)();
                        return this.height += 25;
                    }
                }
            }],
        };

        this.chart = new Chart(context, options);
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (!this.data) return;

        this.data = changes.data.currentValue;
        this.update();
    }

    async update() {
        await this.updateTranslation();
        this.chart.data = this.getData();
        this.chart.resize();
        this.chart.update();
    }

    ngOnDestroy(): any {
        if (this.chart) {
            this.chart.destroy();
            this.chart = void 0;
        }

        this.langChangeSubscription.unsubscribe();
        this.resizeSubscription.unsubscribe();
    }

    getData(): any {
        return {
            labels: this.getLabels(),
            datasets: [{
                backgroundColor: ["#4EB675", "#D95140"],
                data: this.data,
            }],
        };
    }

    getLabels() {
        return [
            `${this.ham}: ${this.data[0]} (${this.percents[0]}%)`,
            `${this.spam}: ${this.data[1]} (${this.percents[1]}%)`,
        ]
    }

    private async updateTranslation() {
        this.spam = await this.translate.t('dashboard.incidentsAndAlerts.spam_ham.spam');
        this.ham = await this.translate.t('dashboard.incidentsAndAlerts.spam_ham.ham');
    }
}
