import { AfterContentInit, Component, Inject, OnDestroy, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';

import { getColor } from 'app/utils/random-color';
import { ChartsDirective } from './charts.directive';
import { DataLoader, DataLoaderImpl } from 'app/utils/table/data-loader';


@Component({
    selector: 'i360-chart',
    template: `
            <i360-legend [lines]="lines"
                         *ngIf="false"
                         [colors]="colors"
                         [striked]="striked"
                         (onClick)="directive.toggleLine($event)"></i360-legend>
        <div>
            <canvas baseChart
                    [data]="data"
                    [labels]="labels"
                    [lines]="lines"
                    [colors]="colors"></canvas>
        </div>
        `,
    styleUrls: ['./charts.component.scss'],
})
export class ChartsComponent implements AfterContentInit, OnDestroy {
    data: any[];
    lines: string[];
    labels: Date[];
    colors: string[];
    striked: boolean[];
    @ViewChild(ChartsDirective, {static: true}) directive: ChartsDirective;
    private onLoaded = Subscription.EMPTY;
    constructor(@Inject(DataLoaderImpl) public dataLoader: DataLoader) {}

    ngAfterContentInit() {
        this.onLoaded = this.dataLoader.loaded.subscribe(data => this.load(data));
    }
    async load(res: any) {
        this.colors = [];
        this.striked = [];
        this.labels = [];
        this.data = [];
        this.lines = [];
        res[0].forEach((item: any, index: number) => {
            if (index > 0) {
                this.colors.push(getColor(index - 1));
                this.data[index - 1] = [];
            }
        });
        res.forEach((column: any) => {
            this.labels.push(new Date(column[0] * 1000));
            column.forEach((item: any, index: number) => {
                if (index > 0) {  // the first one is the common label, others - values
                    this.data[index - 1].push({x: new Date(column[0] * 1000), y: item});
                }
            });
        });
    }

    ngOnDestroy() {
        this.onLoaded.unsubscribe();
    }
}
