import { Injectable } from '@angular/core';
import { responses } from '@imunify360-api/stubs/dashboard';
import {
    ChartName,
    DashboardAddServerResponseInServer,
    DashboardServerIdsResponseInServer,
    ServerResponse,
} from '@imunify360-api/dashboard';
import { forkJoin, of, Observable, delay, first, map, switchMap } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { LicenseState } from 'app/services/license-state';
import { AppState } from 'app/core/app.service';
import { Subtract } from '@imunify360-api/misc';
import { baseEndpoint as globalBaseEndpoint, TEST_SERVER_ID } from '@imunify360-api/server';
import {
    ActivatedRoute,
    ActivatedRouteSnapshot,
    CanActivate, Router,
    RouterStateSnapshot,
} from '@angular/router';

const baseEndpoint = `${globalBaseEndpoint}dashboard/`;
export const histogramEndpoint = `${baseEndpoint}graphs`;
export const heatmapEndpoint = `${baseEndpoint}heatmap`;
export const addServerIdEndpoint = `${baseEndpoint}servers/add`;
export const deleteServerIdEndpoint = `${baseEndpoint}servers/remove`;
export const listServerIdEndpoint = `${baseEndpoint}servers`;
export const listRecommendationsEndpoint = `${baseEndpoint}events-for-group`;


export enum ChartType {
    heatmap = 'heatmap',
    histogram = 'histogram',
    doughnut = 'doughnut',
}

export enum ServerSeverity {
    poor = 'poor',
    medium = 'medium',
    good = 'good',
    unknown = 'unknown',
}

function serverSeverityFromNumber(num: number): ServerSeverity {
    switch (num) {
        case 3: return ServerSeverity.good;
        case 2: return ServerSeverity.medium;
        case 1: return ServerSeverity.poor;
        default: return ServerSeverity.unknown;
    }
}

export type DashboardBackendResponse = Partial<{
    [K in keyof typeof ChartName]: DashboardResponseData;
}>;

export type DashboardResponseData = Array<Array<number | string>> | null;

export type DashboardResponseItem = {
    type: ChartType,
    data: DashboardResponseData,
};

export type DashboardResponse = {
    [K in keyof typeof ChartName | string]: DashboardResponseItem;
};

export type DashboardServerIdsResponseItem = {
    server_id: string,
    id: string,
    severity: ServerSeverity;
    recommendations: {
        [ServerSeverity.good]: DashboardRecommendation[],
        [ServerSeverity.medium]: DashboardRecommendation[],
        [ServerSeverity.poor]: DashboardRecommendation[],
    },
};

export type DashboardServerIdsResponse = DashboardServerIdsResponseItem[];

export function serverIdsResponseToServerIdsResponseItemList(
        res: DashboardServerIdsResponseInServer,
    ): Array<Subtract<DashboardServerIdsResponseItem, 'severity' | 'recommendations'>> {
    const result = res.result.map(item => {
        return {
            server_id: item.server_id,
            id: item.nodename || item.ip || item.server_id,
        };
    });
    for (const item of result) {
        const duplicates = result
            .filter(possibleDuplicate => possibleDuplicate.id === item.id);
        if (duplicates.length > 1) { // includes item itself
            for (const duplicate of duplicates) {
                duplicate.id = `${duplicate.id} (${duplicate.server_id})`;
            }
        }
    }
    return result;
}

export interface DashboardRecommendation {
    description: string;
    link_text: string;
    link: string | null;
    date: number; // unix date
    severity: number;
    server_id: string;
    parameters: {[K: string]: string | number};
}

export type DashboardRecommendationsResponseInServer = ServerResponse<DashboardRecommendation[]>;

// FIXME
const TEST_MODE = typeof TEST !== 'undefined' && TEST && false;
@Injectable()
export class DashboardService implements CanActivate {
    get id() {
        return TEST_SERVER_ID || this.licenseState.license.value.id;
    }
    constructor(
        private http: HttpClient,
        private licenseState: LicenseState,
        private route: ActivatedRoute,
        private router: Router,
        private appState: AppState,
    ) {
    }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
        if (this.licenseState.eula.value.isFailed()) {
            const eulaUrl = ['/', IMUNIFY_PACKAGE, 'admin', 'eula'];
            this.router.navigate(eulaUrl, {
                relativeTo: this.route
            });
            return false;
        }

        return true;
    }

    getHeatmapCharts(params): Observable<DashboardBackendResponse> {
        return TEST_MODE || this.appState.demo
            ? of(responses.heatmap).pipe(delay(100))
            : this.getCharts(heatmapEndpoint, params);
    }
    getHistogramCharts(params): Observable<DashboardBackendResponse> {
        return TEST_MODE || this.appState.demo
            ? of(responses.histogramGrouped(params.group_by)).pipe(delay(100))
            : this.getCharts(histogramEndpoint, params);
    }
    getCharts(endpoint: string, params) {
        return this.http.get<DashboardBackendResponse>(endpoint, {
            params: {
                charts: params.charts.join(','),
                ...(endpoint === histogramEndpoint ? {group_by: params.group_by} : {}),
                start: params.since,
                end: params.to || Math.round(Date.now() / 1000),
                server_id: params.server_id,
            },
        });
    }
    getServerIds(serverIdsResponse?: Observable<DashboardServerIdsResponseInServer>):
        Observable<DashboardServerIdsResponse> {
        return this.licenseState.license.pipe(
            first(license => !!license.id),
            switchMap(() => {
                if (!serverIdsResponse) {
                    serverIdsResponse = this.http.get<DashboardServerIdsResponseInServer>(
                        listServerIdEndpoint, {
                            params: {
                                server_id: this.id,
                            },
                        },
                    );
                }
                return forkJoin(
                    TEST_MODE
                        ? (responses.serverIds.result[0].server_id = this.id,
                            of(responses.serverIds))
                        : serverIdsResponse,
                    this.appState.demo
                        ? of({result: []})
                        : (TEST_MODE
                            ? of(responses.recommendations)
                            : this.http.get(listRecommendationsEndpoint, {
                                params: {
                                    server_id: this.id,
                                },
                            })),
                ).pipe(map(responses => {
                    // unpacking will destroy types
                    // new versions of rxjs allows to pass object to forkJoin,
                    // allowing unpacking
                    const ids = responses[0] as DashboardServerIdsResponseInServer;
                    const recommendations = responses[1] as
                        DashboardRecommendationsResponseInServer;
                    const result = serverIdsResponseToServerIdsResponseItemList(ids).map(item => {
                        // need to calculate severity for each item on client side
                        const itemRecommendations = recommendations.result
                            .filter(rec => rec.server_id === item.server_id);
                        const severity = Math.min(3, ...itemRecommendations.map(r => r.severity));
                        return {
                            server_id: item.server_id,
                            id: item.id,
                            severity: serverSeverityFromNumber(severity),
                            // as all recommendations are already loaded,
                            // save them to prevent additional requests on server_id change
                            recommendations: itemRecommendations.reduce((result, rec) => {
                                    result[serverSeverityFromNumber(rec.severity)].push(rec);
                                    return result;
                                }, { // need to group recommendations by severity
                                     // because they are displayed separately
                                    [ServerSeverity.poor]: [] as DashboardRecommendation[],
                                    [ServerSeverity.medium]: [] as DashboardRecommendation[],
                                    [ServerSeverity.good]: [] as DashboardRecommendation[],
                                }),
                        };
                    });
                    // need to sort items in following order:
                    // with poor severity to the top, then medium, good, then in alphabetical order
                    result.sort((item1, item2) => {
                        if (item1.severity === item2.severity) {
                            return item1.id < item2.id ? -1 : 1;
                        }
                        // accidentally string representation of severity name
                        // can be compared with <
                        return item1.severity < item2.severity ? 1 : -1;
                    });
                    return result;
                }));
            }),
        );
    }
    addServerId(serverIds: string[])
        : Observable<DashboardAddServerResponseInServer> {
        if (TEST_MODE) {
            return of(responses.errorAddServerId);
        } else {
            return this.licenseState.license.pipe(
                first(license => !!license.id),
                switchMap(() => this.http.post<DashboardAddServerResponseInServer>(
                    addServerIdEndpoint, {
                        server_ids: serverIds,
                    }, {
                        params: {
                            server_id: this.id,
                        },
                    },
                )),
            );
        }
    }
    deleteServerId(serverId: string): Observable<DashboardServerIdsResponseInServer> {
        return (TEST_MODE
            ? of(responses.serverIds)
            : this.http.post<DashboardServerIdsResponseInServer>(
                deleteServerIdEndpoint, {
                    server_ids: [serverId],
                }, {
                    params: {
                        server_id: this.id,
                    },
                },
            )
        );
    }
}
