import {
    DashboardServerIdsResponse,
    DashboardServerIdsResponseItem, DashboardService, ServerSeverity,
} from 'app/components/dashboard/dashboard.service';
import { cache } from 'app/utils/cache';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import {
    concat, of, Observable, Subject,
    catchError, debounceTime, first, map,
    startWith, switchMap, take, takeUntil, tap,
    mergeWith, firstValueFrom, throwError
} from 'rxjs';
import { WidgetDirective } from 'app/utils/table/widgets/widget';
import { LicenseState } from 'app/services/license-state';
import Raven from 'raven-js';
import {
    AfterViewInit,
    Component,
    ElementRef,
    OnDestroy,
    ViewChild,
    ViewEncapsulation,
} from '@angular/core';
import { ModalService } from 'app/utils/modals/modal.service';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { useLoader } from 'app/utils/loader/decorators';
import {
    DeleteServerIdModalComponent,
} from 'app/components/dashboard/delete-server-id-modal/delete-server-id-modal.component';
import {
    AddServerIdModalComponent,
} from 'app/components/dashboard/add-server-id-modal/add-server-id-modal.component';
import { NotificationsService } from 'app/services/notifications';
import { HttpErrorResponse } from '@angular/common/http';
import { GroupNotificationsService } from 'app/services/group-notifications';
import { DashboardServerIdsResponseInServer } from '@imunify360-api/dashboard';
import { FiltersService } from 'app/services/filters';

function getItemByServerId(entries: DashboardServerIdsResponse,
                           serverId: string): DashboardServerIdsResponseItem|null {
    const entry = entries.find(entry => entry.server_id === serverId)!;
    if (entry) {
        return entry;
    }
    return null;
}

@Component({
    selector: 'i360-server-id-widget',
    encapsulation: ViewEncapsulation.None,
    templateUrl: './server-id-widget.component.html',
    styleUrls: ['./server-id-widget.component.scss'],
})
export class ServerIdWidgetComponent implements AfterViewInit, OnDestroy {
    readonly delegatePriority = 3;
    @ViewChild('input', {read: ElementRef, static: true}) input: ElementRef<HTMLInputElement>;
    serverIdEntries = new Subject<DashboardServerIdsResponse>();
    cached = this.serverIdEntries.pipe(cache(Infinity));
    form = this.formBuilder.group({
        domain: this.formBuilder.control('', undefined, [({value}) => {
            return this.cached.pipe(
                map(domains => domains.some(item => item.id === value)
                    ? null : {required: {what: 'domain'}}),
                take(1),
            );
        }]),
    });
    inputFocused = new Subject<string>();
    filteredDomains = this.form.controls.domain.valueChanges.pipe(
        startWith(''),
        debounceTime(100),
        mergeWith(this.inputFocused),
        switchMap(value => this.cached.pipe(
            map(domains => domains.filter(domain => domain.id.includes(value)),
        ))),
    );
    private destroyed = new Subject<void>();
    currentActiveItem: DashboardServerIdsResponseItem;
    poorServersCount = 0;
    constructor(private dashboardService: DashboardService,
                public widget: WidgetDirective,
                public licenseState: LicenseState,
                private loader: LoaderDirective,
                private filtersService: FiltersService,
                private modalService: ModalService,
                private groupNotifications: GroupNotificationsService,
                private notifications: NotificationsService,
                private formBuilder: I360FormBuilder) {
        widget.delegate = this;
        widget.paramName = 'server_id';
        concat(
            this.licenseState.license.pipe(
                first(license => !!license.id),
                tap(license => {
                    this.widget.defaultValue = license.id;
                    this.widget.setValue(this.widget.defaultValue);
                }),
            ),
            this.widget.valueChanged,
        ).pipe(
            switchMap(() => this.cached.pipe(
                take(1),
            )),
        ).subscribe(entries => {
            const item = getItemByServerId(entries, this.widget.value);
            if (item !== null) {
                this.currentActiveItem = item;
                this.form.setValue({domain: this.currentActiveItem.id});
            } else {
                Raven.captureException(
                    new Error('Probably server_id of current server is not present in the list'));
            }
        });
        this.getServerIds().subscribe();
    }
    ngAfterViewInit() {
        this.widget.valueChanged.pipe(takeUntil(this.destroyed))
            .subscribe((reload) => {
                if (reload) {
                    this.filtersService.setParam(this.widget.paramName, this.widget.value);
                }
            });
    }
    getServerIds(predefinedIds?: Observable<DashboardServerIdsResponseInServer>) {
        return this.dashboardService.getServerIds(predefinedIds).pipe(
            catchError(e => {
                this.notifications.error('notifications.internalError');
                throw e;
            }),
            tap(entries => {
                // @ts-ignore
                this.serverIdEntries.next(entries);
                // @ts-ignore
                this.poorServersCount = entries
                    .filter(item => item.severity === ServerSeverity.poor).length;
            }),
            takeUntil(this.destroyed),
        );
    }
    onSubmit() {
        this.cached.pipe(
            take(1),
        ).subscribe(entries => {
            if (document.activeElement === this.input.nativeElement) {
                this.input.nativeElement.blur();
            }
            const entry = entries.find(entry => entry.id === this.form.value.domain)!;
            this.widget.setValue(entry.server_id, true);
        });
    }

    deleteServerId(): void {
        const id = this.widget.value;

        this.cached.pipe(take(1))
            .subscribe(async entries => {
                const domain = getItemByServerId(entries, id)!.id;
                if (await this.openDeleteServerConfirm(domain)) {
                    await this.deleteServer(id);
                }
            });
    }

    private openDeleteServerConfirm(domain: string): Promise<boolean> {
        return this.modalService.open(DeleteServerIdModalComponent, {
            data: domain,
        });
    }

    @useLoader
    private async deleteServer(server_id: string): Promise<void> {
        await firstValueFrom(this.dashboardService.deleteServerId(server_id).pipe(
            tap(entries => {
                this.getServerIds(of({result: entries.result})).subscribe(() => {
                    this.widget.setValue(entries.result[0].server_id, true);
                });
            }),
            catchError(e => {
                this.notifications.error('notifications.internalError');
                return throwError(() => e);
            }),
        ), { defaultValue: undefined });
    }

    async addServerIds(predefinedValue: string[] = [], invalidIds: string[] = []) {
        const result = await this.modalService.open(AddServerIdModalComponent, {
            data: {
                predefinedValue,
                invalidIds,
            },
        });
        if (result !== false) {
            const ids = result.ids;
            await this.addServer(ids);
        }
    }

    @useLoader
    private async addServer(ids: string[]) {
        await firstValueFrom(this.dashboardService.addServerId(ids).pipe(
            tap(response => {
                const entries = response.result;
                const invalidIds = response.errors.map(item => item.server_id);
                this.groupNotifications.forAction('add-server-ids', {
                    succeeded: entries
                        .filter(entry => ids.includes(entry.server_id))
                        .map(entry => entry.nodename || entry.ip!),
                    failed: invalidIds,
                });
                this.getServerIds(of({result: entries})).subscribe(() => {
                    if (response.errors.length) {
                        this.addServerIds(invalidIds, invalidIds);
                    } else {
                        this.widget.setValue(ids[ids.length - 1], true);
                    }
                });
            }),
            catchError((e: HttpErrorResponse) => {
                if (e.status === 401) {
                    this.notifications
                        .error('dashboard.serverIdWidget.addModal.authenticationError');
                    this.addServerIds(ids);
                    return of(undefined);
                } else {
                    this.notifications.error('notifications.internalError');
                    return throwError(() => e);
                }
            }),
        ), { defaultValue: undefined });
    }

    ngOnDestroy() {
        this.destroyed.next();
    }
}
