import { ModalEntry } from 'app/utils/modals/entry';
import { Component, Inject } from '@angular/core';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { I360Validators } from 'app/utils/validator/validators';
import { AppState } from 'app/core/app.service';

interface DialogData {
    predefinedValue: string[];
    invalidIds: string[];
}

@Component({
    selector: 'add-server-id-modal',
    templateUrl: './add-server-id-modal.component.html',
    styleUrls: ['./add-server-id-modal.component.scss'],
})
export class AddServerIdModalComponent extends ModalEntry<DialogData, {ids: string[]}> {
    form = this.formBuilder.group({
        ids: this.formBuilder.control(this.data.predefinedValue, [
            I360Validators.listOfServerId,
            ({value}: {value: string[]}): void|Object => {
                if (value
                    && this.data.invalidIds.length
                    && this.data.invalidIds.some(id => value.includes(id))) {
                    return {
                        listOfServerId: {
                            ids: this.data.invalidIds.filter(id => value.includes(id)),
                        },
                    };
                }
            },
        ]),
    });
    constructor(private formBuilder: I360FormBuilder,
                public appState: AppState,
                @Inject(MAT_DIALOG_DATA) public data: DialogData) {
        super();
        if (this.data.invalidIds.length) {
            this.form.markAsDirty({children: true});
        }
    }
}
