import { Component, Input } from '@angular/core';
import { WidgetDirective } from 'app/utils/table/widgets/widget';
import { SetWidgetValueComponent } from 'app/utils/table/widgets/values/set-value';
import { DataLoader, DataLoaderImpl } from 'app/utils/table/data-loader';
import { Router } from '@angular/router';
import { ServerRequestDirective } from 'app/components/dashboard/request.directive';
import {
    ChartType, DashboardResponse, DashboardResponseData,
} from 'app/components/dashboard/dashboard.service';
import { filter, map, take, tap } from 'rxjs';
import { TranslateService } from 'app/utils/translate/translate.service';
import { ciFormatter } from 'app/utils/helper';


// prevData === undefined - data has never been loaded
// loadedData|async === null - failed to load data
// (loadedData|async).length > 0 - smth has been loaded
// (loadedData|async).length === 0 - no data found
@Component({
    selector: 'i360-chart-tile',
    templateUrl: './chart-tile.component.html',
    styleUrls: ['./chart-tile.component.scss'],
    providers: [{
        provide: DataLoaderImpl,
        useExisting: ChartTileComponent,
    }],
})
export class ChartTileComponent extends WidgetDirective
    implements DataLoader<DashboardResponseData> {
    @Input() heatmapAllowed = true;
    prevData: DashboardResponseData;
    number = '';
    loadedData = this.request.loaded.pipe(
        map(data => data[this.paramName].data),
        tap(data => {
            if (!data || data.length === 0) {
                this.number = '';
            } else {
                this.number = ciFormatter(data.reduce((res, cur) => {
                    // they are actually numbers, but we can not declare proper return type
                    // because of stubs
                    return (res as any as number) + (cur[1] as any as number);
                }, 0));
            }
        }),
    );
    loaded = this.loadedData.pipe(
        filter(data => {
            const allowChartUpdate = data !== this.prevData && !!data && !!data.length;
            this.prevData = data;
            return allowChartUpdate;
        }),
    );
    availableValues = ChartType;
    defaultValue = this.availableValues.histogram;
    descriptionExpanded = false;
    constructor(private request: ServerRequestDirective<DashboardResponse>,
                public translateService: TranslateService,
                private router: Router) {
        super(null, request);
        this.delegate = new SetWidgetValueComponent(this);
        // should help to set prevData to smth except undefined, indicating data loaded state
        this.loadedData.pipe(take(1)).subscribe(() => this.prevData = null);
    }
    hasCountriesInHeatmapExpr =
        ([name, num]: [name: string, num: number]) => name !== '' && num > 0;
    navigate(chartType: ChartType) {
        this.router.navigate([], {
            queryParamsHandling: 'merge',
            queryParams: {
                [this.paramName]: chartType,
            },
        });
    }
}
