import dayjs from 'dayjs';
import { Directive, ElementRef, Input, OnChanges, OnDestroy, SimpleChanges } from '@angular/core';
import { fromEvent, debounceTime } from 'rxjs';
import { Chart, ChartType, registerables, TooltipItem } from 'chart.js';
import { TranslateService } from 'app/utils/translate/translate.service';
import 'chartjs-dayjs-adapter';

@Directive({
    selector: 'canvas[baseChart]',
})
export class ChartsDirective implements OnDestroy, OnChanges {

    @Input() data: number[][] = [];
    @Input() lines: string[] = [];
    @Input() labels: any[] = [];
    @Input() colors: any[] = [];

    chart: any;

    private timeFromFormat: string = '';
    private timeToFormat: string = '';
    private eventsFormat: string = '';

    private langChangeSubscription = this.translate.translate.onLangChange.subscribe(() => {
        this.updateTranslation();
        this.chart.update();  // update ticks
    });

    // native ChartJS resizing doesn't work well in DirectAdmin Evolution skin
    private resizeSubscription = fromEvent(window, 'resize').pipe(
        debounceTime(400),
    ).subscribe(() => {
        this.chart.resize();
    });

    constructor(
        public element: ElementRef,
        public translate: TranslateService,
    ) {
        this.updateTranslation();
        Chart.register(...registerables);

        const context = this.element.nativeElement.getContext('2d');
        const options = {
            type: 'bar' as ChartType,
            data: {
                labels: this.labels,
                datasets: this.getDatasets(),
            },
            options: {
                responsive: false,
                aspectRatio: 1.6666666,
                plugins: {
                    legend: {
                        display: false,
                    },
                    tooltip: {
                        callbacks: {
                            title: (items: TooltipItem<ChartType>[]) => {
                                const t = (dp) => dayjs(dp?.x).format('D MMM LT');
                                const index = items[0].dataIndex;
                                const data = items[0].dataset.data;
                                const from = this.timeFromFormat.replace('{from}', t(data[index]));
                                const to = this.timeToFormat.replace('{to}', t(data[index + 1]));
                                return from + '\n' + to;
                            },
                            label: (item) => this.eventsFormat.replace('{numberOfEvents}',
                                item.formattedValue),
                        },
                        backgroundColor: '#4b5c6b',
                        titleMarginBottom: 7,
                        cornerRadius: 3,
                        caretSize: 0,
                        caretPadding: 20,
                        displayColors: false,
                        yPadding: 14,
                        xPadding: 17,
                        titleFontSize: 13,
                        titleFontFamily: 'Open Sans',
                        bodyFontSize: 13,
                        bodyFontFamily: 'Open Sans',
                        mode: 'x' as 'x',
                        intersect: false,
                    },
                },
                scales: {
                    x: {
                        offset: true,
                        type: 'time' as 'time',
                        time: {
                            displayFormats: {
                                day: 'D MMM',
                                hour: 'D MMM | LT',  // " | " is replaced with a line break below
                                millisecond: 'LT',  // used by autoSkip as "longest format"
                            },
                        },
                        ticks: {
                            autoSkip: true,
                            autoSkipPadding: 8,
                            maxRotation: 0,
                            padding: 10,
                            fontFamily: 'Open Sans',
                            fontColor: '#788896',
                            fontSize: 11,
                            callback: (label: string) => label.split(' | '),  // add line break
                        },
                        grid: {
                            display: false,
                        },
                    },
                    y: {
                        grid: {
                            color: '#e4e7ec',
                            zeroLineColor: '#e4e7ec',
                            lineWidth: 2,
                            zeroLineWidth: 2,
                            drawBorder: false,
                        },
                        ticks: {
                            maxTicksLimit: 5,
                            fontColor: '#788896',
                            fontSize: 11,
                            padding: 11,
                            fontFamily: 'Open Sans',
                            beginAtZero: true,
                            callback: (value: number): any => {
                                if (value % 1 === 0) {
                                    return value;
                                }
                            },
                        },
                    },
                },
            },
        };

        this.chart = new Chart(context, options);
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (!this.data) return;

        this.colors = changes.colors.currentValue;
        this.data = changes.data.currentValue;
        this.lines = changes.lines.currentValue;

        this.chart.data.labels = changes.labels.currentValue;
        this.chart.data.datasets = this.getDatasets();
        this.chart.resize();
        this.chart.update();
    }

    ngOnDestroy(): any {
        if (this.chart) {
            this.chart.destroy();
            this.chart = void 0;
        }
        this.langChangeSubscription.unsubscribe();
        this.resizeSubscription.unsubscribe();
    }

    getDatasets(): any {
        return this.data.map((data, index) => {
            return {
                data,
                label: this.lines[index],
                backgroundColor: this.colors[index],
                borderColor: this.colors[index],
                barPercentage: 0.95,
                categoryPercentage: 0.95,
            };
        });
    }

    toggleLine(index: number) {
        let meta = this.chart.getDatasetMeta(index);
        meta.hidden = meta.hidden === null ? true : null;
        this.chart.update();
    }

    private async updateTranslation() {
        // Ugly. We can't have async tooltip callbacks nor custom async tooltip
        this.timeFromFormat = await this.translate.t('dashboard.charts.timeFrom', {from: '{from}'});
        this.timeToFormat = await this.translate.t('dashboard.charts.timeTo', {to: '{to}'});
        this.eventsFormat = await this.translate.t(
            'dashboard.charts.events', {numberOfEvents: '{numberOfEvents}'});
    }
}
