import { XHR } from 'app/utils/xhr/xhr';
import { max, range } from 'd3-array';
import {
    combineLatest, fromEvent, identity, merge, of, Subscription,
    debounceTime, filter, tap,
} from 'rxjs';
import { geoMercator, geoPath, GeoPath, GeoProjection } from 'd3-geo';
import { zoom as d3Zoom, D3ZoomEvent } from 'd3-zoom';
import { interpolateRgb } from 'd3-interpolate';
import { scaleQuantize } from 'd3-scale';
import { AfterViewInit, Component, ElementRef, Inject, OnDestroy, ViewChild } from '@angular/core';
import { polygonContains } from 'd3-polygon';
import { select, Selection } from 'd3-selection';
import { CountryService } from 'app/services/country';
import { DataLoader, DataLoaderImpl } from 'app/utils/table/data-loader';
import { ciFormatter } from 'app/utils/helper';
import polylabel from 'polylabel';
import { useLoader } from 'app/utils/loader/decorators';
import { LoaderDirective } from 'app/utils/loader/loader.directive';

enum ArrowPosition {
    bottom, left, right, top,
}
const PIN_FONT_SIZE = 13;
const PIN_FONT_WEIGHT = 600;
const PIN_ARROW_SIZE = 6;
const INTERPOLATE_MAX = '#38ab63';
const INTERPOLATE_MIN = '#e0e0e0';
const BORDER_COLOR = '#6657ff';
const FILL_COLOR = '#4b5c6b';
const TEXT_COLOR = '#ffffff';
const FILL_OPACITY = 1;
const BACKGROUND_COLOR = '#f4f5f7';
abstract class Pin {
    text: string;
    width: number; // visible width
    height: number; // visible height
    r: number; // visible border radius
    /**
     * center of circle, top-left corner of rectangle
     */
    x: number;
    y: number;
    /**
     * dimensions after applying scaling
     */
    w: number;
    h: number;
    arrowPosition: ArrowPosition;
    padding: number; // padding-left and padding-right of text
    /**
     * center of circle, bottom-middle if rect
     * empty array if is not visible
     */
    center: number[];
    value: number; // number inside pin

    constructor(public map: MapsComponent) {
    }

    calculateBaseDimensions() {
        this.w = this.width / this.map.lastScale;
        this.h = this.height / this.map.lastScale;
        this.x = this.center[0] - this.w / 2;
        this.y = this.center[1] - this.h;
    }

    /**
     * @param other other Pin
     * @param intersection distance which can be covered with another pin
     * @returns {boolean} true if they intersect
     * https://developer.mozilla.org/kab/docs/Games/Techniques/2D_collision_detection
     */
    intersects(other: Pin, intersection = 7 / this.map.lastScale) {
        return this.x + intersection <= other.x + intersection + other.w - intersection &&
            this.x + intersection + this.w - intersection >= other.x + intersection &&
            this.y + intersection <= other.y + intersection + other.h - intersection &&
            this.h - intersection + this.y + intersection >= other.y + intersection;
    }
}
class GroupPin extends Pin {
    padding = 8;

    constructor(a: Pin, b: Pin) {
        super(a.map);
        this.value = a.value + b.value;
        this.text = ciFormatter(this.value);
        this.width = this.height = this.map.getTextWidth(this.text) + this.padding * 2;
        const sizeRelation = a.value / b.value;
        this.center = [
            (sizeRelation * a.center[0] + b.center[0]) / (1 + sizeRelation),
            (sizeRelation * a.center[1] + b.center[1]) / (1 + sizeRelation),
        ];
        this.calculateBaseDimensions();
        this.r = this.w / 2;
    }

    calculateBaseDimensions() {
        super.calculateBaseDimensions();
        this.y += this.h / 2;
    }
}
class SinglePin extends Pin {
    height = 35;
    r = 3;
    count = 1;
    padding = 12;
    constructor(map: MapsComponent,
                public coordinates,
                public name,
                public value) {
        super(map);
        this.text = `${this.name ? `${this.name}:` : ''} ${ciFormatter(this.value)}`;
        this.width = map.getTextWidth(this.text) + this.padding * 2;
    }

    calculateBaseDimensions() {
        const bounds = this.map.screenBounds;
        const containedBounds = bounds.filter(p => polygonContains(this.coordinates, p));
        const coordinates = this.coordinates.filter(p => polygonContains(bounds, p));
        if (containedBounds.length > 1 && containedBounds.length < 4) {
            coordinates.push(...containedBounds.slice(1));
        } else {
            coordinates.push(...containedBounds);
        }
        if (coordinates.length < 4) {
            this.center = [];
        } else {
            this.center = polylabel([coordinates]);
            super.calculateBaseDimensions();
            const bounds = this.map.screenBounds;
            const minX = bounds[0][0];
            const maxX = bounds[2][0];
            const minY = bounds[0][1];
            const arrowSize = PIN_ARROW_SIZE / this.map.lastScale;
            this.y -= arrowSize;
            if (this.x < minX) {
                this.arrowPosition = ArrowPosition.left;
                this.x = this.center[0] + arrowSize;
                this.y = this.center[1] - this.h / 2;
            } else if (this.x + this.w > maxX) {
                this.arrowPosition = ArrowPosition.right;
                this.x = this.center[0] - this.w - arrowSize;
                this.y = this.center[1] - this.h / 2;
            } else if (this.y < minY) {
                this.arrowPosition = ArrowPosition.top;
                this.y = this.center[1] + arrowSize;
            } else {
                this.arrowPosition = ArrowPosition.bottom;
            }
        }
    }

    intersects(pin: Pin, intersects = 4 / this.map.lastScale) {
        return super.intersects(pin, intersects);
    }
}


@Component({
    selector: 'i360-map',
    templateUrl: './maps.component.html',
    styleUrls: ['./maps.component.scss'],
})
export class MapsComponent implements AfterViewInit, OnDestroy {
    static counter = 0;
    id = 'i360-map' + MapsComponent.counter++;
    onLoaded = Subscription.EMPTY;
    path: GeoPath<any, any>;
    maxZoomLevel: number = 50;
    /**
     * A number between 1 and maxZoomLevel,
     * where 1 represents max zoom out and maxZoomLevel - max zoom in.
     */
    lastScale: number;
    initialPins: SinglePin[] = [];
    width: number;
    height: number;
    countriesG: Selection<SVGGElement, any, SVGGElement, any>;
    markersG: Selection<SVGGElement, any, SVGGElement, any>;
    g: Selection<SVGGElement, any, SVGSVGElement, any>;
    stub: CanvasRenderingContext2D;
    projection: GeoProjection;
    svg: Selection<SVGSVGElement, any, SVGGElement, any>;
    zoomEndTimeout; // zone task
    screenBounds: Array<[number, number]>;
    @ViewChild('stub', {static: true}) canvas: ElementRef<HTMLCanvasElement>;
    constructor(private xhr: XHR,
                private countryService: CountryService,
                private loader: LoaderDirective,
                @Inject(DataLoaderImpl) private dataLoader: DataLoader) {}

    getTextWidth(text: string) {
        return this.stub.measureText(text).width;
    }

    getValue(data, d) {
        const found = data.find(c => c[0] === d.properties.code);
        return found ? found[1] : 0;
    }

    browserAbsolutePointToSvgTransitionCoordinates(x, y) {
        const container = this.g.node() as SVGGElement | any;
        const node =  this.svg.node();
        if (!node) {
            return [];
        }

        let point = node.createSVGPoint();
        point.x = x;
        point.y = y;
        if (container) {
            point = point.matrixTransform(container.getScreenCTM().inverse());
        }
        return [point.x, point.y];
    }

    updateScreenBounds(): void|Array<any> {
        const node = this.svg.node();
        if (!node) {
            return [];
        }
        const {top, left, right, bottom} = node.getBoundingClientRect();
        const [minX, minY] = this.browserAbsolutePointToSvgTransitionCoordinates(left, top);
        const [maxX, maxY] = this.browserAbsolutePointToSvgTransitionCoordinates(right, bottom);
        this.screenBounds = [
            [minX, minY],
            [minX, maxY],
            [maxX, maxY],
            [maxX, minY],
        ];
    }

    groupMarkers() {
        this.updateScreenBounds();
        let current: any[] = this.initialPins.filter(pin => {
            pin.calculateBaseDimensions();
            return pin.center.length;
        });
        let next: Pin[] = [];
        let pin1: Pin, pin2: Pin;
        let intersected;
        while (true) {
            intersected = false;
            for (let i = 0; i < current.length; i++) {
                pin1 = current[i];
                if (!pin1) continue;
                for (let j = i + 1; j < current.length; j++) {
                    pin2 = current[j];
                    if (!pin2) continue;
                    if (pin1.intersects(pin2)) {
                        next.push(new GroupPin(pin1, pin2));
                        current[j] = current[i] = null;
                        intersected = true;
                        break;
                    }
                }
                if (current[i]) {
                    next.push(pin1);
                    current[i] = null;
                }
            }
            if (intersected) {
                current = next;
                next = [];
            } else {
                return next;
            }
        }
    }

    removeMarkers() {
        this.markersG.selectAll('*').remove();
    }

    createMarkers() {
        const next = this.groupMarkers();
        if (!next.length) return;
        const single = next.filter(pin => pin instanceof SinglePin);
        const group = next.filter(pin => pin instanceof GroupPin);
        /**
         * Bodies for group pins
         */
        this.markersG.selectAll()
            .data(group).enter()
            .append('circle')
            .attr('fill', FILL_COLOR)
            .attr('stroke', BORDER_COLOR)
            .attr('stroke-width', 4 / this.lastScale)
            .attr('cx', d => d.center[0])
            .attr('cy', d => d.center[1])
            .attr('r', d => d.r)
            .attr('fill-opacity', FILL_OPACITY);
        /**
         * Bodies for single pins
         */
        this.markersG.selectAll()
            .data(single).enter()
            .append('path')
            .attr('d', ({x, y, w, h, r, arrowPosition}) => {
                // https://www.w3schools.com/graphics/svg_path.asp
                const arrowSize = PIN_ARROW_SIZE / this.lastScale;
                const borderRadius = r / this.lastScale;
                const baseRect = [
                    `M ${x + w - borderRadius} ${y + h}`,
                    `Q ${x + w} ${y + h} ${x + w} ${y + h - borderRadius}`,  // bottom-right corner
                    `L ${x + w} ${y + borderRadius}`, // right line
                    `Q ${x + w} ${y} ${x + w - borderRadius} ${y}`,  // top-right corner
                    `L ${x + borderRadius} ${y}`, // top line
                    `Q ${x} ${y} ${x} ${y + borderRadius}`,  // top-left corner
                    `L ${x} ${y + h - borderRadius}`, // left line
                    `Q ${x} ${y + h} ${x + borderRadius} ${y + h}`,  // bottom-left corner
                    `L ${x + w} ${y + h}`, // bottom line
                    `Z`,
                ];
                switch (arrowPosition) {
                    case ArrowPosition.top:
                        baseRect.splice(4, 1,
                            `L ${x + w / 2 + arrowSize} ${y}`,
                            `L ${x + w / 2} ${y - arrowSize}`,
                            `L ${x + w / 2 - arrowSize} ${y}`,
                        );
                        break;
                    case ArrowPosition.right:
                        baseRect.splice(2, 1,
                            `L ${x + w} ${y + h / 2 + arrowSize}`,
                            `L ${x + w + arrowSize} ${y + h / 2}`,
                            `L ${x + w} ${y + h / 2 - arrowSize}`,
                        );
                        break;
                    case ArrowPosition.left:
                        baseRect.splice(6, 1,
                            `L ${x} ${y + h / 2 - arrowSize}`,
                            `L ${x - arrowSize} ${y + h / 2}`,
                            `L ${x} ${y + h / 2 + arrowSize}`,
                        );
                        break;
                    case ArrowPosition.bottom:
                        baseRect.splice(8, 1,
                            `L ${x + w / 2 - arrowSize} ${y + h}`,
                            `L ${x + w / 2} ${y + h + arrowSize}`,
                            `L ${x + w / 2 + arrowSize} ${y + h}`,
                        );
                        break;
                    default:
                        throw new Error('not implemented');
                }
                return baseRect.join(' ');
            })
            .attr('fill', FILL_COLOR)
            .attr('fill-opacity', FILL_OPACITY);
        /**
         * Text for all pins
         */
        this.markersG.selectAll()
            .data(next).enter()
            .append('text')
            .text(d => d.text)
            .attr('x', d => d.x + d.padding / this.lastScale)
            .attr('y', d => d.y + d.h / 2)
            .attr('dy', '0.35em')
            .attr('fill', TEXT_COLOR)
            .style('font-size', `${PIN_FONT_SIZE / this.lastScale}px`)
            .style('font-weight', PIN_FONT_WEIGHT);

    }

    zoomEnd() {
        this.createMarkers();
    }

    zoomStart() {
        this.removeMarkers();
    }

    zoomed(event: D3ZoomEvent<any, any>) {
        this.zoomStart();
        const dims = event.transform;
        const scale = dims.k;
        this.g.style('stroke-width', 1 / scale)
            .attr('transform', `translate(${dims.x},${dims.y}) scale(${dims.k})`);
        this.lastScale = scale;
        clearTimeout(this.zoomEndTimeout);
        this.zoomEndTimeout = setTimeout(() => this.zoomEnd(), 400);
    }
    @useLoader
    async createCountries(data: Array<[string, number]>) {
        const countries = (await import('./topojson')).countries;
        const maxValue = max(data, d => d[1]) || 0;
        const interpolateColor = interpolateRgb(INTERPOLATE_MIN, INTERPOLATE_MAX);
        const quantize = scaleQuantize()
            .domain([0, maxValue])
            .range(range(0, 1, 1 / 150).map(i => <any>interpolateColor(i)));
        const country = this.countriesG.selectAll('*').data(countries);
        country.enter().append('path')
            .attr('d', this.path)
            .style('stroke', BACKGROUND_COLOR);
        this.countriesG.selectAll('*')
            // @ts-ignore
            .style('fill', (d) => {
                const value = this.getValue(data, d);
                return quantize(value);
            });

        this.initialPins = countries.filter(d => data.find(c => c[0] === d.properties.code))
            .map((feature) => {
                let coordinates;
                // use the biggest polygon in the list
                if (feature.geometry.type === 'MultiPolygon') {
                    const lengths = feature.geometry
                        .coordinates.map(coords => coords[0].length);
                    const max = Math.max(...lengths);
                    const index = lengths.indexOf(max);
                    coordinates = feature.geometry.coordinates[index];
                } else {
                    coordinates = feature.geometry.coordinates;
                }
                const value = this.getValue(data, feature);
                const name = this.countryService.getCountryNameByCode(feature.properties.code);
                return new SinglePin(
                    this,
                    coordinates[0].map(c => this.projection(c)),
                    name,
                    value,
                );
            });
    }
    initMap() {
        const map = document.getElementById(this.id)!;
        this.lastScale = 1;
        this.width = Math.floor(map.getBoundingClientRect().width);
        this.height = Math.floor(this.width / 1.6666666);
        const zoom = d3Zoom()
            .scaleExtent([1, this.maxZoomLevel])
            .translateExtent([[0, 0], [this.width, this.height]])
            .on('zoom', (event) => this.zoomed(event));
        const root = select(`#${this.id}`);
        root.selectAll('*').remove();
        this.svg = <any>root
            .insert('svg')
            .attr('width', this.width)
            .attr('height', this.height)
            .attr('style', `background-color: ${BACKGROUND_COLOR}; border-radius: 6px`)
            .call(zoom);
        this.g = <any>this.svg.insert('g');
        this.countriesG = <any>this.g.insert('g');
        this.markersG = <any>this.g.insert('g');
        this.projection = geoMercator()
            .translate([this.width / 2, this.height / 1.6])
            .scale(this.width / 2 / Math.PI);
        this.path = geoPath()
            .projection(this.projection);
    }

    ngAfterViewInit() {
        this.stub = this.canvas.nativeElement.getContext('2d')!;
        this.stub.font = `${PIN_FONT_WEIGHT} ${PIN_FONT_SIZE}px "Open Sans", sans-serif`;
        this.onLoaded = combineLatest([
            this.dataLoader.loaded,
            merge(
                of(null),
                fromEvent(window, 'resize').pipe(
                    debounceTime(1000),
                ),
            ).pipe(
                tap(() => this.initMap()),
            ),
            this.countryService.loaded.pipe(filter(identity)),
        ]).subscribe(async data => {
            await this.createCountries(data[0]);
            this.removeMarkers();
            this.createMarkers();
        });
    }

    ngOnDestroy() {
        this.onLoaded.unsubscribe();
    }
}
