import { AfterContentInit, Directive, NgZone, Optional } from '@angular/core';
import { ServerResponse } from '@imunify360-api/dashboard';
import {
    CommonWidgetsContainerDirective,
} from 'app/utils/table/request/common-widgets-container.directive';
import { ActivatedRoute, Router } from '@angular/router';
import { AbstractRequestDirective } from 'app/utils/table/request/request.directive';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { first } from 'rxjs';
import { GetDataFn, WidgetsOwnerDirective } from 'app/utils/table/request/misc';
import { LicenseState } from 'app/services/license-state';

@Directive({
    selector: '[i360ServerRequest]',
    providers: [{
        provide: WidgetsOwnerDirective,
        useExisting: ServerRequestDirective,
    }],
})
export class ServerRequestDirective<E, R extends ServerResponse<E> = ServerResponse<E>>
    extends AbstractRequestDirective<E, R, GetDataFn<R>>
    implements AfterContentInit {
    constructor(private licenseState: LicenseState,
                route: ActivatedRoute,
                router: Router,
                zone: NgZone,
                @Optional() commonWidgetsContainer: CommonWidgetsContainerDirective,
                loader: LoaderDirective) {
        super(route, router, zone, commonWidgetsContainer, loader);
    }
    ngAfterContentInit() {
        // get server_id from license, requested with config by sentry service
        this.licenseState.license.pipe(
            first(license => !!license.id),
        ).subscribe(() => super.ngAfterContentInit());
    }
    _load(res: R) {
        this.setData(res.result);
    }
}
