import {
    AfterViewInit,
    Component,
    OnDestroy,
    OnInit,
    QueryList,
    ViewChild,
    ViewChildren,
    ViewEncapsulation,
} from '@angular/core';
import {
    combineLatest,
    EMPTY,
    forkJoin,
    of,
    Subject,
    timer,
    catchError,
    debounceTime,
    map,
    repeatWhen,
    switchMap,
    takeUntil
} from 'rxjs';
import { ChartType, DashboardResponse, DashboardService,
} from 'app/components/dashboard/dashboard.service';
import { ChartName } from '@imunify360-api/dashboard';
import { ChartTileComponent } from 'app/components/dashboard/chart-tile/chart-tile.component';
import { ServerRequestDirective } from 'app/components/dashboard/request.directive';
import { FlatTimeframeWidgetComponent } from 'app/utils/table/widgets/content/flat/flat-timeframe';
import { LicenseAPI, Package } from '@imunify360-api/license';
import { getArrayInObject } from 'app/utils/helper';
import { DoughnutTileComponent } from 'app/components/dashboard/doughnut/doughnut-tile.component';
import { XHR } from 'app/utils/xhr/xhr';
import { REFRESH_INTERVAL } from 'app/components/installation/slider/installation-slider.component';

export const timeframeToGroupByMapping = {
    '24h': 'hour',
    '7d': '6h',
    '30d': 'day',
};

@Component({
    selector: 'i360-dashboard',
    encapsulation: ViewEncapsulation.None,
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.scss'],
})
export class DashboardComponent implements AfterViewInit, OnInit, OnDestroy {
    savedItems: DashboardResponse | null;
    imunify360Package = Package.imunify360;
    ChartName = ChartName;
    isEmpty = false;
    @ViewChildren(ChartTileComponent) tiles: QueryList<ChartTileComponent>;
    @ViewChildren(DoughnutTileComponent) doughnutTiles: QueryList<DoughnutTileComponent>;
    fullTiles: ChartTileComponent[] = [];
    @ViewChild(ServerRequestDirective, {static: true})
    request: ServerRequestDirective<DashboardResponse>;
    @ViewChild(FlatTimeframeWidgetComponent, {static: true})
    timeframe: FlatTimeframeWidgetComponent;
    private previousParams = {};
    private destroyed = new Subject<void>();

    constructor(
        private dashboardService: DashboardService,
        private xhr: XHR,

    ) {}

    ngOnInit(): void {
        // For the trigger all interceptors
        this.versionRequest();
    }

    ngOnDestroy(): void {
        this.destroyed.next();
        this.destroyed.complete();
    }

    ngAfterViewInit() {
        combineLatest(
            [
                ...this.tiles.toArray().map(tile => tile.loadedData),
                ...this.doughnutTiles.toArray().map(tile => tile.loadedData),
            ],
        ).pipe(
            debounceTime(0),
        ).subscribe(data => {
            this.isEmpty = data.every(item => {
                if (!item) {
                    return false;
                } else if (item.length === 0) {
                    return true;
                }
                return false;
            });
            this.fullTiles = this.tiles.filter(tile => !!tile.number);
        });
    }
    addGroupBy(params: any) {
        // need to extract timeframe value since here timeframe
        // was already unpacked into since and for
        params['group_by'] = <any>timeframeToGroupByMapping[<any>this.timeframe.widget.value];
        return params;
    }

    /**
     * Optimize server participation, do not requesting
     * data if it was loaded earlier, in some cases
     */
    loadChartData = (_params: {
        [K in keyof ChartName]: ChartType
    }) => {
        const params = {
            [ChartType.heatmap]: {
                charts: [] as string[],
            },
            [ChartType.histogram]: {
                charts: [] as string[],
            },
        };
        if (_params[ChartName.spam_ham]) {
            _params[ChartName.ham] = _params[ChartName.spam_ham];
            _params[ChartName.spam] = _params[ChartName.spam_ham];
            delete _params[ChartName.spam_ham];
        }
        const entries = Object.entries(_params);
        // if only chart type has changed
        // (e.g. there was a switch histogram=>heatmap for some chart)
        // only that one specific chart can be loaded
        // but if something other was changes (server_id, timeframe)
        // need to reload all the charts
        if (entries.some(([key, value]) => {
            return !(key in ChartName) && this.previousParams[key] !== value;
        })) {
            this.savedItems = null;
        }
        this.previousParams = _params;
        for (const [paramName, paramValue] of entries) {
            if (paramName in ChartName) {
                if (!this.savedItems || this.savedItems[paramName].type !== paramValue) {
                    // ChartName parameter should be added only if that data was not loaded before
                    // or there is no saved data
                    // (or common parameter, e.g. server_id, timeframe has changed)
                    params[paramValue].charts.push(paramName);
                }
            } else {
                // server_id, timeframe should be added to both of requests
                params.heatmap[paramName] = paramValue;
                params.histogram[paramName] = paramValue;
            }
        }
        return forkJoin([
            (params.histogram.charts.length
                // group_by parameter needed only for histogram
                ? this.dashboardService.getHistogramCharts(this.addGroupBy(params.histogram))
                : of(null)),
            (params.heatmap.charts.length
                ? this.dashboardService.getHeatmapCharts(params.heatmap)
                : of(null)),
        ]).pipe(
            catchError(() => of([
                null,
                null,
            ])),
            map(([line, heatmap]) => {
                if (this.savedItems === null) {
                    this.savedItems = {};
                }
                for (const name of params.histogram.charts) {
                    this.savedItems[name] = {
                        type: ChartType.histogram,
                        // line is null if there was an error requesting data
                        // line[name] can not be null?
                        data: line && line[name],
                    };
                }
                for (const name of params.heatmap.charts) {
                    this.savedItems[name] = {
                        type: ChartType.heatmap,
                        data: heatmap && heatmap[name],
                    };
                }
                const spam = getArrayInObject(line, 'spam');
                const ham = getArrayInObject(line, 'ham');
                if (this.savedItems) {
                    this.savedItems['spam_ham'] = {
                        type: ChartType.doughnut,
                        data: (ham || spam) ? [[ham, spam]] : [],
                    };
                }

                // adapter, since server usually returns 'result' field
                return {result: this.savedItems};
            }),
        );
    }

    versionRequest() {
        timer(0).pipe(
            switchMap(() => this.xhr.post(LicenseAPI.version())),
            catchError(() => EMPTY),
            repeatWhen((notifications) => notifications.pipe(
                switchMap(() => timer(REFRESH_INTERVAL)),
            )),
            takeUntil(this.destroyed),
        ).subscribe();
    }
}
