import { Component, EventEmitter, Input, Output } from '@angular/core';


@Component({
    selector: 'i360-legend',
    template: `
        <ul>
            <li *ngFor="let line of lines;let i=index"
                    [class.striked]="striked[i]"
                    (click)="clicked(i)">
                <span class="circle" [ngStyle]="{ 'border-color': colors[i] }"></span>
                <span>{{ line }}</span>
            </li>
        </ul>
    `,
    styleUrls: ['./legend.component.scss'],
})
export class LegendComponent {
    @Input() lines: string[];
    @Input() colors: string[];
    @Input() striked: boolean[] = [];
    @Output() onClick = new EventEmitter();

    clicked(index: number) {
        this.onClick.emit(index);
        this.striked[index] = !this.striked[index];
    }
}
