import { Component, Input } from '@angular/core';
import { ServerSeverity } from 'app/components/dashboard/dashboard.service';
import { TranslateService } from 'app/utils/translate/translate.service';
import {
    RecommendationsComponent,
} from 'app/components/dashboard/recommendations/recommendations.component';

@Component({
    selector: 'i360-recommendations-list',
    template: `
        <ng-container *ngIf="recommendationsComponent.item && items.length">
            <div class="i360-recommendations-title i360-severity-{{severity}}"
                 role="heading"
                 aria-level="4">
                {{ 'dashboard.recommendations.lists.titles.' + severity | translate }}
            </div>
            <div *ngFor="let item of items; let index = index"
                 class="i360-recommendation">
                <div class="i360-recommendations-list-order">#{{index + 1}}</div>
                <div>
                    <div class="i360-recommendations-list-date">
                        {{ (item.date * 1000) | dayjs: 'L LTS' }}
                    </div>
                    <div class="i360-recommendations-list-text">
                        <i360-default-if-not-translated
                            [key]="'Noop: '"><!--This key is already ignored-->
                            <ng-template #default>{{ item.description }}</ng-template>
                        </i360-default-if-not-translated>
                    </div>
                    <a class="i360-dashboard-link"
                       *ngIf="item.link"
                       target="_blank"
                       [href]="item.link">
                        <i360-default-if-not-translated
                                [key]="'Noop: '">
                            <ng-template #default>{{ item.link_text }}</ng-template>
                        </i360-default-if-not-translated>
                    </a>
                </div>
            </div>
        </ng-container>
    `,
    styleUrls: ['./recommendations-list.component.scss'],
})
export class RecommendationsListComponent {
    @Input() severity: ServerSeverity;
    get items() {
        return this.recommendationsComponent.item.recommendations[this.severity];
    }
    constructor(public recommendationsComponent: RecommendationsComponent,
                public translate: TranslateService) {}
}
