import { Subscription } from 'rxjs';
import { AfterContentInit, Component, Inject, OnDestroy } from '@angular/core';
import { DataLoader, DataLoaderImpl } from 'app/utils/table/data-loader';

@Component({
    selector: 'i360-doughnut',
    template: `<div><canvas baseDoughnut [data]="data" [percents]="percents"></canvas></div>`,
})
export class DoughnutComponent implements AfterContentInit, OnDestroy {
    data: any[];
    percents: any[];
    onLoaded = Subscription.EMPTY;
    constructor(@Inject(DataLoaderImpl) private dataLoader: DataLoader) {}

    ngAfterContentInit() {
        this.onLoaded = this.dataLoader.loaded.subscribe(data => this.load(data));
    }
    async load(res: any) {
        this.data = [
            (res[0][0] || []).reduce((a, b) => Number(a) + Number(b[1]), 0),
            (res[0][1] || []).reduce((a, b) => Number(a) + Number(b[1]), 0),
        ];
        this.percents = [
            (this.data[0] / (this.data[0] + this.data[1]) * 100).toFixed(1),
            (this.data[1] / (this.data[0] + this.data[1]) * 100).toFixed(1),
        ];
    }
    ngOnDestroy() {
        this.onLoaded.unsubscribe();
    }
}
